import React, { useState, useEffect } from 'react';
import { Button, Nav } from 'react-bootstrap';
import { useParams, useHistory } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';
import { useSelector, useDispatch } from 'react-redux';
import './Detail.css';

function Detail_coffee(props) {

    let history = useHistory();
    let { id } = useParams();
    let state = useSelector((state) => state);
    let dispatch = useDispatch();
    let 찾은상품 = props.coffee.find((a) => { return a.id == id });
    let [tap, tapSet] = useState(0);
    let [alert, setAlert] = useState(true);

    useEffect(() => {
        let timer = setTimeout(() => {
            setAlert(false);
        }, 3000);
        return () => { clearTimeout(timer) }
    }, []);

    return (
        <div className="container detail">
            {
                alert === true
                    ? <div className="alert">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>
                    : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={require('./img/coffee' + id + '.png').default} width="100%" />
                </div>
                <div className="col-md-6 mt-5">
                    <h3>{찾은상품.title}</h3>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}</p>
                    <Button variant="primary" className="m-1" onClick={() => {
                        dispatch({ type: '항목추가', payload: { id: 찾은상품.id, title: 찾은상품.title, quan: 1, price: 찾은상품.price } });
                        history.push('/cart');
                    }}>주문하기</Button>
                    <Button variant="danger" className="m-1" onClick={() => { history.goBack(); }}>뒤로가기</Button>
                </div>
            </div>

            <Nav className="mt-5" variant="tabs" defaultActiveKey="/home">
                <Nav.Item>
                    <Nav.Link onClick={() => { tapSet(0) }}>INFO</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { tapSet(1) }}>SHIPPING</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { tapSet(2) }}>REVIEW</Nav.Link>
                </Nav.Item>
            </Nav>

            <CSSTransition in={true} className="tap" timeout={1000}>
                <Tapcontent tap={tap} />
            </CSSTransition>

        </div>
    );
}

function Tapcontent(props) {
    if (props.tap === 0) { return <div className="mt-5">First</div> }
    else if (props.tap === 1) { return <div className="mt-5">Second</div> }
    else if (props.tap === 2) { return <div className="mt-5">Third</div> }
}

export default Detail_coffee;