import React, { useState } from 'react';
import { Table, Button } from 'react-bootstrap';
import { connect, useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
function Cart() {

    let state = useSelector((state) => state);
    let dispatch = useDispatch();
    console.log(state);
    let total = 0;

    return (
        <div className="cart container">
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Product</th>
                        <th>Quantity</th>
                        <th>Price</th>
                        <th>Total</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        state.reducer.map((a, i) => {
                            total = total + a.price * a.quan;
                            return (
                                <tr>
                                    <td>{i}</td>
                                    <td>{a.title}</td>
                                    <td>
                                        <span className="mr-4">{a.quan}</span>
                                        <Button variant="secondary" size="sm" className="mr-1" onClick={() => {
                                            dispatch({ type: '수량증가', payload: i });
                                        }}>+</Button>
                                        <Button variant="secondary" size="sm" onClick={() => {
                                            dispatch({ type: '수량감소', payload: i });
                                        }}>-</Button>
                                    </td>
                                    <td>{a.price}</td>
                                    <td>{a.price * a.quan}</td>
                                </tr>
                            );
                        })
                    }
                    <tr>
                        <td colSpan="5" className="text-right pr-4"><strong>TOTAL : {total}</strong></td>
                    </tr>
                </tbody>
            </Table>
            <Button className="m-2" variant="primary" size="sm"><Link to="/product" className="text-white">ADD</Link></Button>
            <Button className="m-2" variant="primary" size="sm">ORDER</Button>
        </div>
    )
}

export default Cart;